@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;400;500;600;700;800&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

html {
	min-width: 1140px;
}

.App {
	font-family: "Montserrat", sans-serif;
	background-color: #f1f1f1;
}

nav {
	height: 50px;

	display: flex;
	z-index: 999;
	justify-content: space-around;
	background: #fff(101, 243, 7);
	color: rgb(75, 73, 73);
	border-bottom: 1px solid rgb(204, 204, 204);
	box-shadow: 0px 1px 6px rgb(184, 182, 182);
}

.logo {
	padding: 11px;
	font-size: 1.05em;
	font-weight: 800;
	align-items: center;
	text-transform: uppercase;
	display: flex;
	justify-content: center;
	letter-spacing: 0.5px;
}

.logo a {
	color: rgb(241, 49, 43);
}

.logo:hover {
	cursor: pointer;
}

.Link-Container {
	width: 60%;
	height: 100%;
	display: flex;
	justify-content: space-around;
	align-items: center;
	/* border: 1px solid green; */
}

.Link-Container a {
	text-align: center;
	font-size: 87%;
	font-weight: 500;
	padding: 0.4rem 1rem;
	text-decoration: none;
	text-transform: uppercase;
	color: inherit;
	letter-spacing: 0.07rem;
	word-spacing: 0.07rem;
}

.Link-Container a:hover {
	padding: 0.4.5rem 0rem;
	background-color: #dddddd;
	border-radius: 3px;
	transition: all 0.3s ease-in-out;
	color: inherit;
}

/* .cards {
	display: flex;
	align-items: flex-start;
	justify-content: center;
} */

.card {
	background: #fff;
	border: 0.5px solid rgb(235, 233, 233);
	width: 25em;
	border-radius: 0.4em;
	margin: 1em;
	overflow: hidden;
	cursor: pointer;
	box-shadow: 0 13px 27px -5px hsla(240, 30.1%, 28%, 0.25),
		0 8px 16px -8px hsla(0, 0%, 0%, 0.3), 0 -6px 16px -6px hsla(0, 0%, 0%, 0.03);
	transition: all ease 200ms;
}

.card:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
	box-shadow: 0 13px 40px -5px hsla(240, 30.1%, 28%, 0.12),
		0 8px 32px -8px hsla(0, 0%, 0%, 0.14),
		0 -6px 32px -6px hsla(0, 0%, 0%, 0.02);
}

.card img {
	padding: 3.5px;
	width: 100%;
	object-fit: cover;
	background-color: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
}

.card h2 {
	color: #222;
	margin-top: -0.2em;
	line-height: 1.4;
	font-size: 1.15em;
	font-weight: 500;
	font-family: "Montserrat", sans-serif;
	transition: all ease-in 100ms;
}

.card p {
	color: #777;
	font-size: 97%;
}

.card h5 {
	color: #bbb;
	font-weight: 700;
	font-size: 0.7em;
	letter-spacing: 0.04em;
	margin: 1.4em 0 0 0;
	text-transform: uppercase;
}

.card-body {
	padding: 1.2em;
}

